* {
  font-family: "Red Rose", cursive;
  box-sizing: border-box;
  margin: 0;
}

.meta {
  width: 188px;
}
.main {
  /* border: 1px solid red; */
  display: flex;
  margin: 50px 5%;
  align-items: left;
  text-align: left;
  padding: 20px 0;
}
.main-a {
  width: 50%;
}

.pics {
  /* border: 2px solid black; */
  display: flex;
  width: 50%;
  text-align: center;
}
.pics1 {
  margin-left: 20%;
  margin-right: 10px;
  /* border: 1px solid yellow; */
  text-align: right;
  padding-top: 120px;
}
.pics2 {
  padding-top: 40px;
  /* border: 1px solid green; */
  margin-right: 40px;
}
.tail {
  background: linear-gradient(90deg, #a02279 11.45%, #a02279 11.45%);
  display: flex;
  flex-direction: row;
  gap: 20%;
  align-items: center;
  margin: 0 5%;
}
.sponsor {
  margin: auto;
  width: 20%;
}
.sub-btn {
  background: linear-gradient(90deg, #a02279 11.45%, #a02279 11.46%);
  border-radius: 0px 8px 8px 0px;
  border: 1px solid #a02279;
  color: white;
  padding: 10px;
  width: 30%;
}
.search-txt {
  background: rgba(247, 247, 247, 0.1);
  border: 1px solid #a3a3a3;
  border-radius: 8px 0px 0px 8px;
  padding: 10px;
  width: 70%;
}
.search {
  margin-top: 12%;
}
.bolden {
  color: #a02279;
  font-weight: bold;
}
.hdr {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 5%;
  /* border: 1px solid; */
}
.nav {
  margin: 30px 5%;
  padding: 0 5px;
  display: flex;
  flex-direction: row;
  width: 50%;
  text-align: center;
  /* justify-content: center; */
}
ul {
  margin-right: 10px;
  /* border: 1px solid; */
  margin-top: 15px;
  /* padding-right: auto; */
  width: 100%;
}
li {
  display: inline-block;
  margin: 0 30px;
}
li a {
  text-decoration: none;
  color: black;
}
/* #navbar{
  display: inline ;
 border: 5px solid;
  margin: 0 auto;
} */
.meta-logo {
  text-align: left;
}
.main-heading {
  font-size: 3.2rem;
}
.main-para {
  font-size: 1.5rem;
}
.tile {
  margin-bottom: 10px;
  width: 100%;
}

.connect-btn {
  background: linear-gradient(90deg, #a02279 11.45%, #a02279 11.46%);
  border-radius: 7px;
  border: 1px solid #a02279;
  color: white;
  padding: 10px;
  width: auto;
}
.inspira {
  text-align: center;
  margin: 60px 0;
  font-weight: bold;
}
.card-container {
  display: flex;
  gap: 10px;
  align-items: center;
  flex-wrap: wrap;
  margin: 0 5%;
  padding-bottom: 80px;
}
.my-card {
  border: 1px solid;
  width: auto;
  border-radius: 20px;
  padding: 15px 0;
}
.info1 {
  text-align: left;
  display: flex;
  flex-direction: column;
}
.card-img {
  margin: 0 5%;
}
.nft {
  margin: 0;
  background: linear-gradient(90deg, #a02279 11.45%, #a02279 11.45%);

  color: white;
  display: flex;
  justify-content: center;
  padding-bottom: 200px;
}
.left {
  width: 40%;
}
.left h1 {
  margin-left: 15%;
  margin-top: 150px;
  margin-bottom: 30px;
  font-weight: bold;
}
.left p {
  margin-left: 15%;
}
.left button {
  margin-left: 15%;
}
.right {
  width: 60%;
  justify-content: right;
  margin-top: 100px;
}
.nft-img {
  width: 60%;
  margin-left: 200px;
}
.btn-learn {
  color: linear-gradient(90deg, #a02279 11.45%, #a02279 11.46%);
  border-radius: 8px;
  border: 1px solid #a02279;
  background: white;
  padding: 10px;
  width: 156px;
}
.btn-learn:hover {
  cursor: pointer;
}
.footer {
  background: #1d1d1e;
  color: white;
  display: flex;
  justify-content: center;
  flex-wrap: wrap-reverse;
}
.ft-left {
  width: 35%;
  padding-left: 5%;
  padding-top: 50px;
}
.ft-right {
  width: 65%;
  display: flex;
  padding-top: 50px;
  justify-content: space-between;
  padding-right: 180px;
}
.ft-right ul {
  display: flex;
  flex-direction: column;
}
.list-head {
  font-weight: bold;
  text-align: left;
  margin-bottom: 25px;
}
.list-item {
  font-size: 0.8rem;
  margin-bottom: 10px;
}
.socials {
  display: flex;
  gap: 18px;
  margin-bottom: 35px;
}
.meta-white {
  margin-bottom: 70px;
}
.locations {
  margin: 0 5%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 40px;
  padding-right: 14px;
}
.locations span {
  padding-top: 11px;
}
.nav-mobile {
  display: flex;
  flex-direction: column;

  /* border: 1px solid; */
  text-align: center;
  justify-content: center;
  /* overflow: visible; */
  /* animation: drop 2s ease-in; */
  position: fixed;

  background: #fff;
  box-shadow: 10px 5px 5px #f7f7f7;
}
#hamburger {
  display: none;
}
.sample-card {
  border: 1px solid #d7d7d7;
  border-radius: 15px;
  padding: 15px 0 5px;
  font-size: 0.8rem;
  /* font-weight: 500; */
  color: #434343;
  width: 292px;
}
.card-left {
  display: inline;
  width: 35%;
  margin-left: 15px;
}
.card-right {
  display: inline;
  width: 65%;
  margin-right: 15px;
}

.rights {
  text-align: right;
  margin-bottom: 4px;
}
.lefts {
  margin-bottom: 4px;
}
.lefts img {
  padding: 0 3px;
}
.bottom-half {
  display: flex;
}
.pic {
  z-index: 0;
  margin-left: 15px;
  margin-right: auto;
  margin-bottom: -20px;
}
.pic-div {
  border: 1px solid red;
  text-align: center;
  justify-content: center;
}
.heart {
  z-index: 0;
  left: 240px;
  bottom: 245px;
  margin-top: 10px;
  position: relative;
  margin-right: 10px;
  /* opacity: 0.2; */
}
.heart:hover {
  cursor: pointer;
  background-color: #a02279;
}
@media (max-width: 800px) {
  .m-logo {
    margin-left: auto;
    margin-right: auto;
  }
  .main-a {
    /* border: 1px solid red; */
    display: block;
    text-align: center;
    margin: auto;
    width: 95%;
    font-size: 2rem;
  }
  .pics {
    /* border: 2px solid red; */
    display: none;
  }
  .connect-btn {
    width: 10%;
  }
  .main-heading {
    font-size: 2rem;
    width: 95%;
  }
  .search {
    font-size: 0.8rem;
  }
  .footer {
    display: flex;
    /* flex-direction: column; */
  }
  .ft-right {
    display: block;
    width: 100vw;
  }
  .ft-left {
    width: 100vw;
    margin: 0 auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-left: 10%;
  }
  .meta-white {
    width: 60vw;
  }
  .nav,
  .connect-btn {
    display: none;
  }
  #hamburger {
    display: flex;
    flex-direction: column;
  }
  .card-container {
    justify-content: center;
  }
  .locations {
    display: flex;
    flex-direction: column;
  }
  .sort {
    margin-top: 5px;
    width: 35%;
  }
  .nft {
    display: flex;
    flex-direction: column;
  }

  .left {
    width: 100%;
    padding-left: 5%;
    padding-right: 5%;
  }
  .right {
    display: none;
  }
}
@media (max-width: 1009px) {
  .card-container {
    justify-content: space-evenly;
  }
}
@media (max-width: 12009px) {
  .card-container {
    justify-content: space-evenly;
  }
}
